import axios from 'axios';
import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {CSSTransition, TransitionGroup} from 'react-transition-group';
import '../../../assets/Common.css';
import './Infomation.css';

import {
    Card,
    CardTitle,
    CardBody,
    Col,
    Row,
    Table,
    Progress
} from 'reactstrap';
import {Button, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';

class BasicInfomantion extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userData: []
        };
    }

    componentDidMount() {
        this.loadData()
    }

    componentDidUpdate() {}

    CustomCard = ({label, user}) => {

        var level = 'success';

        if (user.abnormalHrData === '미측정' && user.depressData === '미측정' && user.hrData === '미측정' && user.recoveryData === '미측정' && user.spo2Data === '미측정' && user.stepData === '미측정' && user.stressData === '미측정') {
            level = 'secondary';
        }

        if (user.depressData === '경도' || user.recoveryData === '보통') {
            level = 'warning';
        }

        if (user.abnormalHrData === 'Y' || user.depressData === '심각' || user.depressData === '중증도' || user.hrData > 60 || user.hrData < 50 || user.recoveryData === '낮음' || user.spo2Data > 95 || user.spo2Data < 93 || user.stepData === 0 || user.stressData === 'Y') {
            level = 'danger';
        }

        //console.log('level : ' + level);

        return (
            <Card color={level} className='horizontal-card'>
                <CardBody className="mb-0 p-1">
                    {/* <CardTitle className="text-white mb-0">{label}</CardTitle> */}
                    <p className="text-white mb-0">{label}</p>
                </CardBody>
                <CustomModal selectedUser={user}/>
            </Card>
        );
    };

    CustomProgressLabels = (props) => {
        var danger = 0;
        var warning = 0;
        var good = 0;
        var noData = 0;

        return (
            <div>
                {
                    this
                        .state
                        .userData
                        .map((item, index) => {
                            var level = 'success';

                            if (item.abnormalHrData === '미측정' && item.depressData === '미측정' && item.hrData === '미측정' && item.recoveryData === '미측정' && item.spo2Data === '미측정' && item.stepData === '미측정' && item.stressData === '미측정') {
                                level = 'secondary';
                            }

                            if (item.depressData === '경도' || item.recoveryData === '보통') {
                                level = 'warning';
                            }

                            if (item.abnormalHrData === 'Y' || item.depressData === '심각' || item.depressData === '중증도' || item.hrData > 60 || item.hrData < 50 || item.recoveryData === '낮음' || item.spo2Data > 95 || item.spo2Data < 93 || item.stepData === 0 || item.stressData === 'Y') {
                                level = 'danger';
                            }

                            if (level === 'success') 
                                good++;
                            else if (level === 'secondary') 
                                noData++;
                            else if (level === 'warning') 
                                warning++;
                            else if (level === 'danger') 
                                danger++;
                            }
                        )
                }
                <p className='mb-0'>전체 사용자 건강 상태(전체 {good + danger + warning + noData}명)</p>
                <Progress className="progress-bar-rounded" multi="multi">
                    <Progress
                    animated
                        bar="bar"
                        color="success"
                        value={good / (good + danger + warning + noData) * 100}>양호 {Math.round(good / (good + danger + warning + noData) * 100)}% ({good}명)</Progress>
                    <Progress
                    animated
                        bar="bar"
                        color="warning"
                        value={warning / (good + danger + warning + noData) * 100}>주의 {Math.round(warning / (good + danger + warning + noData) * 100)}% ({warning}명)</Progress>
                    <Progress
                    animated
                        bar="bar"
                        color="danger"
                        value={danger / (good + danger + warning + noData) * 100}>위험 {Math.round(danger / (good + danger + warning + noData) * 100)}% ({danger}명)</Progress>
                    <Progress
                    animated
                        bar="bar"
                        color="secondary"
                        value={noData / (good + danger + warning + noData) * 100}>미측정 {Math.round(noData / (good + danger + warning + noData) * 100)}% ({noData}명)</Progress>
                </Progress>
                <div className='boader-dark-gray mt-2'></div>
            </div>
        );
    };

    async loadData() {
        axios
            .get('https://api.ssirc.dev/api/User/')
            .then(userResponse => {
                const usersWithAdditionalData = [];

                Promise
                    .all(userResponse.data.map(user => {
                        return Promise
                            .all([
                                axios.get(`https://api.ssirc.dev/api/Hr/${user.User_id}/`),
                                axios.get(`https://api.ssirc.dev/api/Spo2/${user.User_id}/`),
                                axios.get(`https://api.ssirc.dev/api/Abnormal_hr/${user.User_id}/`),
                                axios.get(`https://api.ssirc.dev/api/Stress/${user.User_id}/`),
                                axios.get(`https://api.ssirc.dev/api/Depress/${user.User_id}/`),
                                axios.get(`https://api.ssirc.dev/api/Step/${user.User_id}/`),
                                axios.get(`https://api.ssirc.dev/api/Recovery/${user.User_id}/`)
                            ])
                            .then(([
                                hrResponse,
                                spo2Response,
                                abnormalHrResponse,
                                stressResponse,
                                depressResponse,
                                stepResponse,
                                recoveryResponse
                            ]) => {
                                var lastHrData = hrResponse.data[hrResponse.data.length - 1];
                                var lastSpo2Data = spo2Response.data[spo2Response.data.length - 1];
                                var lastAbnormalHrData = abnormalHrResponse.data[abnormalHrResponse.data.length - 1];
                                var lastStressData = stressResponse.data[stressResponse.data.length - 1];
                                var lastDepressData = depressResponse.data[depressResponse.data.length - 1];
                                var lastStepData = stepResponse.data[stepResponse.data.length - 1];
                                var lastRecoveryData = recoveryResponse[recoveryResponse.data.length - 1];

                                //to do : null 처리
                                lastHrData = lastHrData
                                    ? lastHrData.Hr
                                    : '미측정';
                                lastSpo2Data = lastSpo2Data
                                    ? lastSpo2Data.Spo2
                                    : '미측정';
                                lastAbnormalHrData = lastAbnormalHrData
                                    ? (lastAbnormalHrData.Abnormal_hr === true)
                                        ? 'Y'
                                        : 'N'
                                    : '미측정';
                                lastStressData = lastStressData
                                    ? (lastStressData.Stress === true)
                                        ? 'Y'
                                        : 'N'
                                    : '미측정';
                                lastDepressData = lastDepressData
                                    ? lastDepressData.Depress
                                    : '미측정';
                                lastStepData = lastStepData
                                    ? lastStepData.Step
                                    : '미측정';
                                lastRecoveryData = lastRecoveryData
                                    ? lastRecoveryData.Recovery
                                    : '미측정';

                                if (lastDepressData === 3) 
                                    lastDepressData = "심각";
                                else if (lastDepressData === 2) 
                                    lastDepressData = "중증도";
                                else if (lastDepressData === 1) 
                                    lastDepressData = "경도";
                                else if (lastDepressData === 0) 
                                    lastDepressData = "정상";
                                
                                if (lastRecoveryData === 2) 
                                    lastRecoveryData = "높음";
                                else if (lastRecoveryData === 1) 
                                    lastRecoveryData = "보통";
                                else if (lastRecoveryData === 0) 
                                    lastRecoveryData = "낮음";
                                
                                user.Gps_location = user.Gps_location
                                    ? user.Gps_location
                                    : 'no-idea';

                                const userWithAdditionalData = {
                                    ...user,
                                    hrData: lastHrData,
                                    spo2Data: lastSpo2Data,
                                    abnormalHrData: lastAbnormalHrData,
                                    stressData: lastStressData,
                                    depressData: lastDepressData,
                                    stepData: lastStepData,
                                    recoveryData: lastRecoveryData
                                };
                                usersWithAdditionalData.push(userWithAdditionalData);
                            })
                            .catch(
                                error => console.error(`Error fetching data for user ${user.User_id}`, error)
                            );
                    }))
                    .then(() => {
                        this.setState({userData: usersWithAdditionalData});
                        console.log('userData with additional data: ', usersWithAdditionalData);
                    });
            })
            .catch(userError => console.error('Error fetching userData', userError));
    }

    render() {
        return (
            <Fragment>
                <TransitionGroup>
                    <CSSTransition
                        component="div"
                        className="TabsAnimation"
                        appear={true}
                        timeout={0}
                        enter={false}
                        exit={false}>

                        <div className='whole-page-ui'>
                            <Row>
                                <Col className='font-size-xxlarge vertical-center'>전체 사용자 보기</Col>
                                <Col className='font-size-small vertical-center horizontal-right'>HOME &gt; 사용자 현황 관리 &gt; 전체 사용자 보기</Col>
                            </Row>
                            <div className='space-10px'></div>
                            <div className='boader-black'></div>
                            <div className='space-20px'></div>
                            <Row>
                                <Col md='1' className='ms-auto'>
                                    <Link to={`../../Members/Basic/`}>
                                        <Card className="font-icon-wrapper">
                                            <i className='pe-7s-back'></i>
                                            <p>{'돌아가기'}</p>
                                        </Card>
                                    </Link>
                                </Col>
                            </Row>
                            <Row>
                                <Col className='mb-3'>
                                    <this.CustomProgressLabels/>
                                </Col>
                            </Row>
                            <Row>
                                {
                                    this
                                        .state
                                        .userData
                                        .map(item => (
                                            <Col key={item.User_id} md={2} className="mb-3">
                                                <this.CustomCard label={item.Name} user={item}/>
                                            </Col>
                                        ))
                                }
                            </Row>
                        </div>
                    </CSSTransition>
                </TransitionGroup>
            </Fragment>
        )
    }
}
export default withRouter(BasicInfomantion);

class CustomModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false
        };

        this.toggle = this
            .toggle
            .bind(this);
    }

    toggle() {
        console.log('toggle called!');
        this.setState({
            modal: !this.state.modal
        });
    }

    DetailInfoButton = ({user}) => {

        var level = 'success';

        if (user.abnormalHrData === '미측정' && user.depressData === '미측정' && user.hrData === '미측정' && user.recoveryData === '미측정' && user.spo2Data === '미측정' && user.stepData === '미측정' && user.stressData === '미측정') {
            level = 'secondary';
        }

        if (user.depressData === '경도' || user.recoveryData === '보통') {
            level = 'warning';
        }

        if (user.abnormalHrData === 'Y' || user.depressData === '심각' || user.depressData === '중증도' || user.hrData > 60 || user.hrData < 50 || user.recoveryData === '낮음' || user.spo2Data > 95 || user.spo2Data < 93 || user.stepData === 0 || user.stressData === 'Y') {
            level = 'danger';
        }

        return (
            <Button
                style={{
                    marginTop: '0px',
                    paddingLeft: '8px',
                    paddingRight: '8px',
                    paddingTop: '2px',
                    paddingBottom: '2px',
                    borderColor: 'white'
                }}
                color={level}
                onClick={this.toggle}>
                상세정보
            </Button>
        );
    };

    render() {
        const {selectedUser} = this.props;

        var hrDataBgColor = 'custom-color-success';
        var spo2DataBgColor = 'custom-color-success';
        var stepDataBgColor = 'custom-color-success';
        var stressDataBgColor = 'custom-color-success';
        var recoveryDataBgColor = 'custom-color-success';
        var depressDataBgColor = 'custom-color-success';
        var abnormalHrDataBgColor = 'custom-color-success';
        
        if (selectedUser.hrData === '미측정') hrDataBgColor = 'custom-color-secondary';
        else if (selectedUser.hrData > 60 || selectedUser.hrData < 50) hrDataBgColor = 'custom-color-danger';

        if (selectedUser.spo2Data === '미측정') spo2DataBgColor = 'custom-color-secondary';
        else if (selectedUser.spo2Data > 95 || selectedUser.spo2Data < 93) spo2DataBgColor = 'custom-color-danger';

        if (selectedUser.stepData === '미측정') stepDataBgColor = 'custom-color-secondary';
        else if (selectedUser.stepData === 0) stepDataBgColor = 'custom-color-danger';

        if (selectedUser.stressData === '미측정') stressDataBgColor = 'custom-color-secondary';
        else if (selectedUser.stressData === 'Y') stressDataBgColor = 'custom-color-danger';

        if (selectedUser.recoveryData === '미측정') recoveryDataBgColor = 'custom-color-secondary';
        else if (selectedUser.recoveryData === '보통') recoveryDataBgColor = 'custom-color-warning';
        else if (selectedUser.recoveryData === '낮음') recoveryDataBgColor = 'custom-color-danger';

        if (selectedUser.depressData === '미측정') depressDataBgColor = 'custom-color-secondary';
        else if (selectedUser.depressData === '경도') depressDataBgColor = 'custom-color-warning';
        else if (selectedUser.depressData === '심각' || selectedUser.depressData === '중증도') depressDataBgColor = 'custom-color-danger';

        if (selectedUser.abnormalHrData === '미측정') abnormalHrDataBgColor = 'custom-color-secondary';
        else if (selectedUser.abnormalHrData === 'Y') abnormalHrDataBgColor = 'custom-color-danger';

        return (
            <span className="d-inline-block mb-2">
                <this.DetailInfoButton user={selectedUser}/>
                <Modal
                    isOpen={this.state.modal}
                    toggle={this.toggle}
                    className={this.props.className}
                    backdrop={true}>
                    <ModalHeader toggle={this.toggle}>{selectedUser && `${selectedUser.Name}님의 상세정보`}
                    </ModalHeader>
                    <ModalBody>
                        <Table className="mb-0" bordered={true}>
                            <tbody>
                                <tr>
                                    <td className='custom-table-color'>이름</td>
                                    <td>{selectedUser.Name}</td>
                                    <td className='custom-table-color'>아이디</td>
                                    <td>{selectedUser.Id}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>핸드폰번호</td>
                                    <td>{selectedUser.Hp_num}</td>
                                    <td className='custom-table-color'>보호자 핸드폰번호</td>
                                    <td>{selectedUser.Superv_hp_num}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>심박수</td>
                                    <td className={hrDataBgColor}>{selectedUser.hrData}</td>
                                    <td className='custom-table-color'>산소포화도</td>
                                    <td className={spo2DataBgColor}>{selectedUser.spo2Data}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>걸음수</td>
                                    <td className={stepDataBgColor}>{selectedUser.stepData}</td>
                                    <td className='custom-table-color'>스트레스</td>
                                    <td className={stressDataBgColor}>{selectedUser.stressData}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>회복탄력성</td>
                                    <td className={recoveryDataBgColor}>{selectedUser.recoveryData}</td>
                                    <td className='custom-table-color'>우울증</td>
                                    <td className={depressDataBgColor}>{selectedUser.depressData}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>불규칙 심박수</td>
                                    <td className={abnormalHrDataBgColor}>{selectedUser.abnormalHrData}</td>
                                    <td className='custom-table-color'></td>
                                    <td></td>
                                </tr>
                            </tbody>
                        </Table>
                    </ModalBody>
                </Modal>
            </span>
        );
    }
}
